import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../../actions";
import Grid from "@material-ui/core/Grid";
import CircularProgress from "@material-ui/core/CircularProgress";

import keygen from "../../utils/helpers/keygen";
import Item from "../item";

const List = () => {
  const { loading, items, failure, filtered, isFiltered } = useSelector(
    (state) => state.app
  );
  const list = isFiltered ? filtered : items;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(actions.getItems());
  }, [dispatch]);
  return (
    <>
      {loading && <CircularProgress />}
      {!loading && !failure && list.length > 0 ? (
        list.map((item) => <Item key={keygen()} item={item} />)
      ) : (
        <Grid item xs={12}>
          There is no item with this filter!
        </Grid>
      )}
    </>
  );
};

export default List;
