import { put, takeLatest, call, select } from "redux-saga/effects";
import * as actions from "../actions";
import * as constants from "../constants";
import * as service from "../utils/services/index";
import * as helpers from "../utils/helpers/array";

const getItems = (state) => state.app.items;
const getFiltered = (state) => state.app.filtered;

export function* getCities() {
  try {
    yield put(actions.loading(true));
    const response = yield call(service.getRequest);
    yield put(actions.setItems(response));
  } catch (error) {
    yield put(actions.failure(true));
  } finally {
    yield put(actions.loading(false));
  }
}
export function* getCitiesSaga() {
  yield takeLatest(constants.GET_ITEMS, getCities);
}

export function* filterItems(action) {
  const { sort, city, province } = action.payload;
  const items = yield select(getItems);

  if (city || province || sort) {
    if (city && province) {
      const clone = [...items];
      const byCity = helpers.search(clone, "city", city);
      const byProvince = helpers.search(byCity, "admin_name", province);
      yield put(actions.setFiltered(byProvince));
    }
    if (city && !province) {
      const clone = [...items];
      const byCity = helpers.search(clone, "city", city);
      yield put(actions.setFiltered(byCity));
    }
    if (province && !city) {
      const clone = [...items];
      const byProvince = helpers.search(clone, "admin_name", province);
      yield put(actions.setFiltered(byProvince));
    }
    yield put(actions.setIsFiltered(true));
  }

  if (sort) {
    const filtered = yield select(getFiltered);
    const sorted = filtered.length > 0 ? [...filtered] : [...items];
    yield put(actions.setFiltered(helpers.sortHelper(sorted, sort)));
  }
}
export function* filterItemsSaga() {
  yield takeLatest(constants.FILTER_ITEMS, filterItems);
}

const Sagas = [getCitiesSaga(), filterItemsSaga()];
export default Sagas;
