export const sortHelper = (array, sortBy) => {
  switch (sortBy) {
    case "cityA":
      return array.sort((a, b) =>
        a.city > b.city ? 1 : b.city > a.city ? -1 : 0
      );
    case "cityZ":
      return array.sort((a, b) =>
        b.city > a.city ? 1 : a.city > b.city ? -1 : 0
      );
    case "populationAsc":
      return array.sort((a, b) => Number(a.population) - Number(b.population));
    case "populationDesc":
      return array.sort((a, b) => Number(b.population) - Number(a.population));
    default:
      return array;
  }
};

export const search = (array, key, value) => {
  return array.filter((item) =>
    item[key].toLowerCase().includes(value.toLowerCase())
  );
};
