import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import * as actions from "../../actions";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

import useStyles from "./styles";

const SearchFilter = () => {
  const classes = useStyles();

  const [state, setState] = useState({
    sort: "",
    city: "",
    province: "",
  });

  const dispatch = useDispatch();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setState({ ...state, ...{ [name]: value } });
  };
  useEffect(() => {
    const { sort, city, province } = state;
    if (sort || city || province) {
      dispatch(actions.filterItems(sort, city, province));
    } else {
      dispatch(actions.setIsFiltered(false));
    }
  }, [state, dispatch]);

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <Typography variant="h6" component="h3">
          Search/Filter:
        </Typography>
        <form className={classes.form} noValidate autoComplete="off">
          <TextField
            name="city"
            label="City..."
            variant="outlined"
            value={state.city}
            onChange={(e) => handleChange(e)}
          />
          <TextField
            name="province"
            label="Province..."
            variant="outlined"
            value={state.province}
            onChange={(e) => handleChange(e)}
          />
          <FormControl variant="outlined">
            <InputLabel id="sort">Sort</InputLabel>
            <Select
              labelId="sort"
              name="sort"
              value={state.sort}
              onChange={(e) => handleChange(e)}
              label="sort"
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              <MenuItem value="cityA">City Name (A to Z )</MenuItem>
              <MenuItem value="cityZ">City Name (Z to A )</MenuItem>
              <MenuItem value="populationAsc">Population (Ascending)</MenuItem>
              <MenuItem value="populationDesc">
                Population (Descending)
              </MenuItem>
            </Select>
          </FormControl>
        </form>
      </Paper>
    </Grid>
  );
};

export default SearchFilter;
