import * as constants from "../constants";

export const getItems = () => ({
  type: constants.GET_ITEMS,
  payload: {},
});
export const setItems = (items) => ({
  type: constants.SET_ITEMS,
  payload: {
    items,
  },
});
export const filterItems = (sort, city, province) => ({
  type: constants.FILTER_ITEMS,
  payload: {
    sort,
    city,
    province,
  },
});
export const setFiltered = (filtered) => ({
  type: constants.SET_FILTERED,
  payload: {
    filtered,
  },
});
export const setIsFiltered = (isFiltered) => ({
  type: constants.SET_IS_FILTERED,
  payload: {
    isFiltered,
  },
});
export const failure = (failure) => ({
  type: constants.FAILURE,
  payload: {
    failure,
  },
});
export const loading = (loading) => ({
  type: constants.LOADING,
  payload: {
    loading,
  },
});
